  flex: 1 1 0%;
}

.before\:bg-white\/\[0\.05\]::before {
  content: var(--tw-content);
  background-color: rgb(255 255 255 / 0.05);
}

.after\:h-px::after {
  content: var(--tw-content);
  height: 1px;
}

.after\:flex-1::after {
  content: var(--tw-content);
  flex: 1 1 0%;
}

.after\:bg-white\/\[0\.05\]::after {
  content: var(--tw-content);
  background-color: rgb(255 255 255 / 0.05);
}

.hover\:bg-\[\#252525\]:hover {
  --tw-bg-opacity: 1;
  background-color: rgb(37 37 37 / var(--tw-bg-opacity, 1));
}

.hover\:bg-\[\#303030\]:hover {
  --tw-bg-opacity: 1;
  background-color: rgb(48 48 48 / var(--tw-bg-opacity, 1));
}

.hover\:bg-gray-200:hover {
  --tw-bg-opacity: 1;
  background-color: rgb(229 231 235 / var(--tw-bg-opacity, 1));
}

.hover\:bg-\[\#252525\]\/50:hover {
  background-color: rgb(37 37 37 / 0.5);
}

.hover\:bg-\[\#303030\]\/50:hover {
  background-color: rgb(48 48 48 / 0.5);
}

.hover\:text-blue-300:hover {
  --tw-text-opacity: 1;
  color: rgb(147 197 253 / var(--tw-text-opacity, 1));
}

.hover\:underline:hover {
  text-decoration-line: underline;
}

.hover\:opacity-90:hover {
  opacity: 0.9;
}

.focus\:border-blue-500:focus {
  --tw-border-opacity: 1;
  border-color: rgb(59 130 246 / var(--tw-border-opacity, 1));
}

.focus\:border-blue-500\/50:focus {
  border-color: rgb(59 130 246 / 0.5);
}

.focus\:outline-none:focus {
  outline: 2px solid transparent;
  outline-offset: 2px;
}

.focus\:ring-2:focus {
  --tw-ring-offset-shadow: var(--tw-ring-inset) 0 0 0 var(--tw-ring-offset-width) var(--tw-ring-offset-color);
  --tw-ring-shadow: var(--tw-ring-inset) 0 0 0 calc(2px + var(--tw-ring-offset-width)) var(--tw-ring-color);
  box-shadow: var(--tw-ring-offset-shadow), var(--tw-ring-shadow), var(--tw-shadow, 0 0 #0000);
}

.focus\:ring-blue-500\/25:focus {
  --tw-ring-color: rgb(59 130 246 / 0.25);
}

@media (min-width: 768px) {

  .md\:grid-cols-6 {
    grid-template-columns: repeat(6, minmax(0, 1fr));
  }
}
